export default function SubAccountsPage() {
  const [subAccounts] = useState<SubAccount[]>(mockSubAccounts)
  const [searchQuery, setSearchQuery] = useState('')
  const [companyFilter, setCompanyFilter] = useState<string>('all')
  const [statusFilter, setStatusFilter] = useState<string>('all')
  const [typeFilter, setTypeFilter] = useState<string>('all')
  const [sortBy, setSortBy] = useState<'name' | 'users' | 'created' | 'activity'>('name')
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('asc')
  const [currentPage, setCurrentPage] = useState(1)
  const [selectedSubAccounts, setSelectedSubAccounts] = useState<string[]>([])
  const itemsPerPage = 15

  // Get unique companies and types
  const companies = Array.from(new Set(subAccounts.map(sa => sa.companyName))).sort()
  const types = Array.from(new Set(subAccounts.map(sa => sa.type))).sort()

  // Filter sub-accounts
  const filteredSubAccounts = subAccounts.filter(subAccount => {
    const matchesSearch = 
      subAccount.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      subAccount.companyName.toLowerCase().includes(searchQuery.toLowerCase()) ||
      subAccount.owner.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      subAccount.owner.email.toLowerCase().includes(searchQuery.toLowerCase())
    
    const matchesCompany = companyFilter === 'all' || subAccount.companyName === companyFilter
    const matchesStatus = statusFilter === 'all' || subAccount.status === statusFilter
    const matchesType = typeFilter === 'all' || subAccount.type === typeFilter
    
    return matchesSearch && matchesCompany && matchesStatus && matchesType
  })

  // Sort sub-accounts
  const sortedSubAccounts = [...filteredSubAccounts].sort((a, b) => {
    let comparison = 0
    
    switch (sortBy) {
      case 'name':
        comparison = a.name.localeCompare(b.name)
        break
      case 'users':
        comparison = a.users - b.users
        break
      case 'created':
        comparison = new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
        break
      case 'activity':
        comparison = new Date(a.lastActivity).getTime() - new Date(b.lastActivity).getTime()
        break
    }
    
    return sortOrder === 'asc' ? comparison : -comparison
  })

  // Pagination
  const totalPages = Math.ceil(sortedSubAccounts.length / itemsPerPage)
  const startIndex = (currentPage - 1) * itemsPerPage
  const paginatedSubAccounts = sortedSubAccounts.slice(startIndex, startIndex + itemsPerPage)

  // Statistics
  const stats = {
    total: subAccounts.length,
    active: subAccounts.filter(sa => sa.status === 'Active').length,
    inactive: subAccounts.filter(sa => sa.status === 'Inactive').length,
    totalUsers: subAccounts.reduce((sum, sa) => sum + sa.users, 0),
    avgUsersPerSubAccount: Math.round(subAccounts.reduce((sum, sa) => sum + sa.users, 0) / subAccounts.length),
  }

  // Handle select/deselect
  const handleSelectAll = () => {
    if (selectedSubAccounts.length === paginatedSubAccounts.length) {
      setSelectedSubAccounts([])
    } else {
      setSelectedSubAccounts(paginatedSubAccounts.map(sa => sa.id))
    }
  }

  const handleSelectSubAccount = (id: string) => {
    setSelectedSubAccounts(prev =>
      prev.includes(id) ? prev.filter(saId => saId !== id) : [...prev, id]
    )
  }

  // Status badge colors
  const getStatusColor = (status: string) => {
    return status === 'Active' ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-800'
  }

  return (
    <div className="p-6">
      {/* Header */}
      <div className="mb-6">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 flex items-center gap-2">
              üè™ Sub-Account Management
            </h1>
            <p className="text-gray-600 mt-1">
              Manage all sub-accounts across all companies
            </p>
          </div>
          <Link
            href="/admin/sub-accounts/new"
            className="bg-purple-600 text-white px-6 py-2 rounded-lg hover:bg-purple-700 flex items-center gap-2"
          >
            <span>‚ûï</span>
            Add Sub-Account
          </Link>
        </div>

        {/* Statistics Cards */}
        <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
          <div className="bg-white p-4 rounded-lg border border-gray-200">
            <div className="text-2xl font-bold text-purple-600">{stats.total}</div>
            <div className="text-sm text-gray-600">Total Sub-Accounts</div>
          </div>
          <div className="bg-white p-4 rounded-lg border border-gray-200">
            <div className="text-2xl font-bold text-green-600">{stats.active}</div>
            <div className="text-sm text-gray-600">Active</div>
          </div>
          <div className="bg-white p-4 rounded-lg border border-gray-200">
            <div className="text-2xl font-bold text-gray-600">{stats.inactive}</div>
            <div className="text-sm text-gray-600">Inactive</div>
          </div>
          <div className="bg-white p-4 rounded-lg border border-gray-200">
            <div className="text-2xl font-bold text-blue-600">{stats.totalUsers}</div>
            <div className="text-sm text-gray-600">Total Users</div>
          </div>
          <div className="bg-white p-4 rounded-lg border border-gray-200">
            <div className="text-2xl font-bold text-orange-600">{stats.avgUsersPerSubAccount}</div>
            <div className="text-sm text-gray-600">Avg Users/Sub-Account</div>
          </div>
        </div>
      </div>

      {/* Filters and Search */}
      <div className="bg-white p-4 rounded-lg border border-gray-200 mb-6">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          {/* Search */}
          <div className="md:col-span-2">
            <input
              type="text"
              placeholder="üîç Search sub-accounts, companies, owners..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
            />
          </div>

          {/* Company Filter */}
          <div>
            <select
              value={companyFilter}
              onChange={(e) => setCompanyFilter(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
            >
              <option value="all">All Companies</option>
              {companies.map(company => (
                <option key={company} value={company}>{company}</option>
              ))}
            </select>
          </div>

          {/* Status Filter */}
          <div>
            <select
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
            >
              <option value="all">All Status</option>
              <option value="Active">Active</option>
              <option value="Inactive">Inactive</option>
            </select>
          </div>
        </div>

        {/* Type Filter and Sort */}
        <div className="flex items-center gap-4 mt-4 pt-4 border-t border-gray-200">
          <div className="flex-1">
            <select
              value={typeFilter}
              onChange={(e) => setTypeFilter(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
            >
              <option value="all">All Types</option>
              {types.map(type => (
                <option key={type} value={type}>{type}</option>
              ))}
            </select>
          </div>
          
          <span className="text-sm text-gray-600">Sort by:</span>
          <div className="flex gap-2">
            {(['name', 'users', 'created', 'activity'] as const).map((option) => (
              <button
                key={option}
                onClick={() => {
                  if (sortBy === option) {
                    setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc')
                  } else {
                    setSortBy(option)
                    setSortOrder('asc')
                  }
                }}
                className={`px-3 py-1 rounded-lg text-sm ${
                  sortBy === option
                    ? 'bg-purple-600 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {option.charAt(0).toUpperCase() + option.slice(1)}
                {sortBy === option && (sortOrder === 'asc' ? ' ‚Üë' : ' ‚Üì')}
              </button>
            ))}
          </div>
        </div>

        {/* Bulk Actions */}
        {selectedSubAccounts.length > 0 && (
          <div className="flex items-center gap-4 mt-4 pt-4 border-t border-gray-200">
            <span className="text-sm text-gray-600">
              {selectedSubAccounts.length} selected
            </span>
            <div className="flex gap-2">
              <button className="px-4 py-1 bg-green-600 text-white rounded-lg text-sm hover:bg-green-700">
                Activate
              </button>
              <button className="px-4 py-1 bg-gray-600 text-white rounded-lg text-sm hover:bg-gray-700">
                Deactivate
              </button>
              <button className="px-4 py-1 bg-blue-600 text-white rounded-lg text-sm hover:bg-blue-700">
                Export
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Sub-Accounts Table */}
      <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-gray-50 border-b border-gray-200">
              <tr>
                <th className="px-4 py-3 text-left">
                  <input
                    type="checkbox"
                    checked={selectedSubAccounts.length === paginatedSubAccounts.length && paginatedSubAccounts.length > 0}
                    onChange={handleSelectAll}
                    className="w-4 h-4 text-purple-600 border-gray-300 rounded focus:ring-purple-500"
                  />
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Sub-Account
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Company
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Owner
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Type
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Users
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Created
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Last Activity
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {paginatedSubAccounts.map((subAccount) => (
                <tr key={subAccount.id} className="hover:bg-gray-50">
                  <td className="px-4 py-4">
                    <input
                      type="checkbox"
                      checked={selectedSubAccounts.includes(subAccount.id)}
                      onChange={() => handleSelectSubAccount(subAccount.id)}
                      className="w-4 h-4 text-purple-600 border-gray-300 rounded focus:ring-purple-500"
                    />
                  </td>
                  <td className="px-4 py-4">
                    <Link href={`/admin/sub-accounts/${subAccount.id}`} className="flex items-center gap-3 hover:underline">
                      <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center text-blue-600 font-bold">
                        {subAccount.name.charAt(0)}
                      </div>
                      <div>
                        <div className="font-medium text-gray-900">{subAccount.name}</div>
                        <div className="text-sm text-gray-500">{subAccount.id}</div>
                      </div>
                    </Link>
                  </td>
                  <td className="px-4 py-4">
                    <Link href={`/admin/companies/${subAccount.companyId}`} className="hover:underline">
                      <div className="font-medium text-gray-900">{subAccount.companyName}</div>
                      <div className="text-sm text-gray-500">{subAccount.companyId}</div>
                    </Link>
                  </td>
                  <td className="px-4 py-4">
                    <div>
                      <div className="font-medium text-gray-900">{subAccount.owner.name}</div>
                      <div className="text-sm text-gray-500">{subAccount.owner.email}</div>
                    </div>
                  </td>
                  <td className="px-4 py-4">
                    <span className="px-2 py-1 bg-blue-100 text-blue-800 rounded-full text-xs font-medium">
                      {subAccount.type}
                    </span>
                  </td>
                  <td className="px-4 py-4">
                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(subAccount.status)}`}>
                      {subAccount.status}
                    </span>
                  </td>
                  <td className="px-4 py-4">
                    <div className="text-center">
                      <div className="font-semibold text-purple-600">{subAccount.users}</div>
                      <div className="text-xs text-gray-500">users</div>
                    </div>
                  </td>
                  <td className="px-4 py-4 text-sm text-gray-900">{subAccount.createdAt}</td>
                  <td className="px-4 py-4 text-sm text-gray-900">{subAccount.lastActivity}</td>
                  <td className="px-4 py-4">
                    <div className="flex gap-2">
                      <Link
                        href={`/admin/sub-accounts/${subAccount.id}`}
                        className="px-3 py-1 bg-purple-600 text-white rounded-lg text-sm hover:bg-purple-700"
                      >
                        View
                      </Link>
                      <Link
                        href={`/admin/sub-accounts/${subAccount.id}/edit`}
                        className="px-3 py-1 bg-blue-600 text-white rounded-lg text-sm hover:bg-blue-700"
                      >
                        Edit
                      </Link>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <div className="bg-gray-50 px-4 py-3 border-t border-gray-200 flex items-center justify-between">
          <div className="text-sm text-gray-600">
            Showing {startIndex + 1} to {Math.min(startIndex + itemsPerPage, sortedSubAccounts.length)} of {sortedSubAccounts.length} sub-accounts
          </div>
          <div className="flex gap-2">
            <button
              onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
              disabled={currentPage === 1}
              className="px-3 py-1 border border-gray-300 rounded-lg text-sm hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Previous
            </button>
            {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
              const page = i + 1
              return (
                <button
                  key={page}
                  onClick={() => setCurrentPage(page)}
                  className={`px-3 py-1 rounded-lg text-sm ${
                    currentPage === page
                      ? 'bg-purple-600 text-white'
                      : 'border border-gray-300 hover:bg-gray-100'
                  }`}
                >
                  {page}
                </button>
              )
            })}
            {totalPages > 5 && <span className="px-2">...</span>}
            <button
              onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
              disabled={currentPage === totalPages}
              className="px-3 py-1 border border-gray-300 rounded-lg text-sm hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Next
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}
